"use strict";

/*---Buttons----*/
var rockBtn = document.getElementById('rock');
var paperBtn = document.getElementById('paper');
var scissorsBtn = document.getElementById('scissors');
var resetBtn = document.getElementById('reset');
var root = document.getElementById('root');
var resultPlace = document.getElementById('result');
var counter = 0;
var raundResult;
var wins = 0;
var looses = 0;
var finalResult;
var first = true;

var getRandom = function getRandom() {
  var types = ['rock', 'paper', 'scissors'];
  return types[Math.floor(Math.random() * types.length)];
};

var getResult = function getResult(random, chosen) {
  if (random === chosen) {
    return 'It is a DRAW!';
  }

  if (chosen === 'rock') {
    return random === 'paper' ? 'You’ve LOST!' : 'You’ve WON!';
  }

  if (chosen === 'paper') {
    return random === 'rock' ? 'You’ve WON!' : 'You’ve LOST!';
  }

  if (chosen === 'scissors') {
    return random === 'rock' ? 'You’ve LOST!' : 'You’ve WON!';
  }
};

var showImg = function showImg(img) {
  var imageShape = document.createElement('img');
  raundResult.appendChild(imageShape);
  imageShape.setAttribute('src', "img/".concat(img, ".jpg"));
};
/*--- Game Logic ---*/


var playGame = function playGame(event) {
  if (raundResult && raundResult.parentNode === root) {
    root.removeChild(raundResult);
  }

  if (counter === 0 && !first) {
    resultPlace.removeChild(finalResult);
    first = true;
  }

  counter++;
  raundResult = document.createElement('div');
  raundResult.setAttribute('id', 'game-result');
  root.appendChild(raundResult);
  var random = getRandom();
  var result = getResult(random, event.target.id);

  if (result === 'You’ve LOST!') {
    looses++;
  }

  if (result === 'You’ve WON!') {
    wins++;
  }

  showImg(event.target.id);
  var getInfo = document.createElement('p');
  raundResult.appendChild(getInfo);
  var getInfoText = document.createTextNode("Round ".concat(counter, ", \n      ").concat(event.target.id, " vs ").concat(random, ", \n      ").concat(result));
  getInfo.appendChild(getInfoText);
  showImg(random);

  if (counter > 2) {
    finalResult = document.createElement('h3');
    resultPlace.appendChild(finalResult);
    var win = "Congratulations you've WON! Your score: ".concat(wins, ":").concat(looses);
    var lose = "Sorry, you've LOST! Your score: ".concat(wins, ":").concat(looses);
    var draw = "It is a DRAW! Your score: ".concat(wins, ":").concat(looses);
    var rez = wins > looses ? win : lose;

    if (wins === looses) {
      rez = draw;
    }

    var text = document.createTextNode(rez);
    finalResult.appendChild(text);
    counter = 0;
    wins = 0;
    looses = 0;
    first = false;
  }
};

var resetGame = function resetGame() {
  counter = 0;
  wins = 0;
  looses = 0;

  if (finalResult && finalResult.parentNode === result) {
    result.removeChild(finalResult);
  }

  if (raundResult && raundResult.parentNode === root) {
    root.removeChild(raundResult);
  }
};
/*-- Events --*/


rockBtn.addEventListener('click', playGame);
paperBtn.addEventListener('click', playGame);
scissorsBtn.addEventListener('click', playGame);
resetBtn.addEventListener('click', resetGame);